const dispatch = async (id, method) => {

    let result = '';

    const url = `https://pht.life/api/v1/member/${method}/`;

    const options = {
        method: "POST",
        body: JSON.stringify({'userId': id}),
        headers: {
            "Content-Type": "application/json",
            "Accept": "application/json",
            "X-Requested-With": "XMLHttpRequest",
        },
        mode: 'cors',
    }

    await fetch(url, options)
        .then((response) => response.json())
        .then((data) => result = data.result)
        .catch((error) => console.log(error));

    return result
}

export const getTopic = async (userId) => {
    const result = await dispatch(userId, 'topic');
    return result === 'error' ? null : result;
}

export const getVKGroup = async (userId) => {
    const result = await dispatch(userId, 'group');
    return result === 'error' ? null : result;
}
